.van-goods-action-button {
  --button-warning-background-color: linear-gradient(to right, #ffd01e, #ff8917);
  --button-warning-background-color: var(--goods-action-button-warning-color, linear-gradient(to right, #ffd01e, #ff8917));
  --button-danger-background-color: linear-gradient(to right, #ff6034, #ee0a24);
  --button-danger-background-color: var(--goods-action-button-danger-color, linear-gradient(to right, #ff6034, #ee0a24));
  --button-default-height: 1.70667rem;
  --button-default-height: var(--goods-action-button-height, 1.70667rem);
  --button-line-height: 0.85333rem;
  --button-line-height: var(--goods-action-button-line-height, 0.85333rem);
  --button-plain-background-color: #fff;
  --button-plain-background-color: var(--goods-action-button-plain-color, #fff);
  --button-border-width: 0;
}
.van-goods-action-button--first {
  margin-left: 0.21333rem;
  --button-border-radius: 21.312rem 0 0 21.312rem;
  --button-border-radius: var(--goods-action-button-border-radius, 21.312rem) 0 0 var(--goods-action-button-border-radius, 21.312rem);
}
.van-goods-action-button--last {
  margin-right: 0.21333rem;
  --button-border-radius: 0 21.312rem 21.312rem 0;
  --button-border-radius: 0 var(--goods-action-button-border-radius, 21.312rem) var(--goods-action-button-border-radius, 21.312rem) 0;
}
.van-goods-action-button--first.van-goods-action-button--last {
  --button-border-radius: 21.312rem;
  --button-border-radius: var(--goods-action-button-border-radius, 21.312rem);
}
.van-goods-action-button--plain {
  --button-border-width: 0.04267rem;
}
.van-goods-action-button__inner {
  width: 100%;
  font-weight: 500 !important;
  font-weight: var(--font-weight-bold, 500) !important;
}
